input {
    outline: none;
}

footer {
    background: #fff;
    width: 100%;
    height: 90px;
    margin-top: 60px;
    border-top: 1px #eee solid;

    .shade {
        position: fixed;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        bottom: 0;
        z-index: 9;
        background: rgba(0, 0, 0, 0.8);
        display: none;
    }

    .forum-footer {
        width: 1200px;
        height: 100%;
        padding: 35px 0;
        margin: 0 auto;

        .f-left {
            float: left;
            width: 700px;
            color: #a1a1a1;

            img {
                width: 88px;
                height: 20px;
            }

            .f-collaborate {
                width: 100%;
                line-height: 35px;

                span {
                    display: block;
                    float: left;
                    margin-right: 20px;
                    font-size: 14px;
                }
            }

            .f-copyright {
                width: 100%;
                line-height: 30px;
                font-size: 12px;
            }
        }

        .f-right {
            width: 200px;
            float: right;

            .share {
                a {
                    position: relative;
                    width: 40px;
                    height: 40px;
                    float: left;
                    background: #f8f8f8;
                    border-radius: 100%;
                    margin: 0 5px;

                    img {
                        position: absolute;
                    }

                    &.wx {
                        img {
                            left: 10px;
                            top: 10px;
                            width: 22px;
                            height: 18px;
                        }
                    }

                    &.telegram {
                        img {
                            left: 10px;
                            top: 10px;
                            width: 19px;
                            height: 15px;
                        }
                    }

                    &.sina {
                        img {
                            left: 10px;
                            top: 10px;
                            width: 20px;
                            height: 16px;
                        }
                    }
                }
            }

            .about-clause {
                font-size: 14px;
                margin-top: 10px;

                a {
                    color: #a1a1a1;
                    float: left;
                    padding: 0 10px;

                    &:hover {
                        color: #333;
                    }
                }

                span {
                    display: block;
                    float: left;
                    width: 5px;
                    height: 10px;
                    border-right: 2px #eee solid;
                    vertical-align: top;
                    margin-top: 3px;
                }
            }
        }
    }

    .login-con {
        margin-left: -(120 * 2 + 342)/2 + px;
        padding: 0 120px 20px;
        position: fixed;
        left: 50%;
        //top: 230px;
        background: #f6f6f6;
        border-radius: 5px;
        width: 342px;
        top: 50%;
        z-index: 10;
        transform: translate(0, -50%);
        display: none;

        .register {
            display: none;
        }

        h1 {
            color: #1979f4;
            font-size: 30px;
            line-height: 74px;
            text-align: center;
        }

        .login-item {
            margin-bottom: 10px;
            background: #fff;
            border: 1px solid #e5e5e5;
            height: 54px;
            width: 340px;

            &.phone-item {
                position: relative;

                .phone-code {
                    float: left;
                    width: 57px;
                    height: 100%;
                    line-height: 54px;
                    color: #474747;
                    font-size: 16px;
                    cursor: pointer;

                    img {
                        vertical-align: middle;
                        margin-left: 10px;
                    }
                }

                input {
                    width: 275px;
                }
            }

            &.auth-code-item {
                input {
                    width: 150px;
                }

                a {
                    float: right;
                    background: #f1f1f1;
                    border-left: 1px solid #d6d6d6;
                    display: block;
                    height: 100%;
                    width: 128px;
                    color: #1979f4;
                    line-height: 54px;
                    text-align: center;
                }
            }

            & > span {
                float: left;
                display: block;
                height: 100%;
                width: 60px;
                text-align: center;

                img {
                    margin-top: 15px;
                    display: inline-block;
                }
            }

            input {
                float: right;
                border: none;
                height: 100%;
                color: #999;
                font-size: 16px;
                width: 275px;
            }
        }

        button {
            background: #1979f4;
            border: none;
            cursor: pointer;
            display: block;
            height: 56px;
            width: 100%;
            color: #fff;
            font-size: 24px;
            line-height: 56px;
            text-align: center;
            text-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.8);
        }

        .login-tips {
            color: #474747;
            font-size: 16px;
            line-height: 50px;
            text-align: center;

            p {
                float: left;
            }

            a {
                color: #1979f4;
            }

            .forget-password {
                float: right;
            }
        }
    }
}
